.Thermometer {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: bold;
}

.Thermometer-Container {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: flex-end;
  margin: 20px;
}

.Thermometer-Indicator-Container {
  width: 30px;
  height: 150px;
  border-radius: 20px;
  background-color: #eeeeee;
  margin-bottom: -30px;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: center;
}

.Thermometer-Indicator {
  background-color: #f34e54;
  width: 75%;
  border-radius: 20px;
  margin-bottom: -20px;
}

.Thermometer-Bulb-Container {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  margin-bottom: 3px;
  background-color: #eee;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Thermometer-Bulb {
  background-color: #f34e54;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin-bottom: -15px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
